    }

    .Register_icon_input__3bwHY {
        color: #bac4c9;
        margin-right: 3%;
    }

    .Register_input__W7adh {
        border: none;
        outline: none;
        font-size: 1rem;
    }

        .Register_input__W7adh::-webkit-input-placeholder {
            font-style: italic;
            color: #bac4c9;
            font-size: 1rem;
        }

        .Register_input__W7adh::-ms-input-placeholder {
            font-style: italic;
            color: #bac4c9;
            font-size: 1rem;
        }

        .Register_input__W7adh::placeholder {
            font-style: italic;
            color: #bac4c9;
            font-size: 1rem;
        }

    .Register_submit_btn__3g3XG {
        background-color: #1f8efa;
        color: white;
        border: none;
        border-radius: 0.3rem;
        padding: 1rem;
        font-size: 1rem;
        font-weight: 700;
        box-shadow: 0px 3px 15px -1px rgba(31,140,250,0.5);
    }

        .Register_submit_btn__3g3XG:hover {
            cursor: pointer;
        }

    .Register_subtitle__2_iyx {
        text-align: center;
        margin-top: 1rem;
        font-size: 1rem;
        color: grey;
    }

        .Register_subtitle_navlink__3GcqV {
            color: #1f8efa;
            font-style: italic;
            text-decoration: none;
            font-size: 1rem;
        }
    
    .Register_error__31P6L {
        text-align: center;
        margin-top: 1rem;
        font-size: 1rem;
        color: rgb(212, 25, 25);
        font-weight: bold;
    }

@media (max-width: 1023px) {

    .Register_form_place__2PHwm {
        width: 35%;
        margin-right: 0;
    }
    
        .Register_emblema__3afna {
            font-size: 2rem;
        }

        .Register_div_input__1oWid {
            padding: .7rem;
        }
      
        .Register_input__W7adh {
            font-size: .7rem;
        }
    
            .Register_input__W7adh::-webkit-input-placeholder {
                font-size: .7rem;
            }
    
            .Register_input__W7adh::-ms-input-placeholder {
                font-size: .7rem;
            }
    
            .Register_input__W7adh::placeholder {
                font-size: .7rem;
            }
    
        .Register_submit_btn__3g3XG {
            font-size: .7rem;
        }
    
        .Register_subtitle__2_iyx {
            font-size: .7rem;
        }
    
            .Register_subtitle_navlink__3GcqV {
                font-size: .7rem;
            }
}

@media (max-width: 767px) {
    .Register_registration_page__3rl5- {
        background: none;
        padding: 2rem;
    }
    
    .Register_form_place__2PHwm {
        width: 95%;
        margin: auto;
    }

    .Register_input__W7adh {
        padding: 0.25rem 2rem 0.25rem 0;
    }
}
